@keyframes change {
  0% {
    background-color: #fde755;
    transform: scale(0.5);
  }
  100% {
    background-color: #f23f5d;
    transform: scale(0.8);
  }
}

#app4.app4 {
  align-items: center;
  display: grid;
  justify-items: center;
  position: relative;
}

#app4 .circle {
  border: 1px solid #f23f5d;
  border-radius: 50%;
  height: 30vw;
  width: 30vw;
}

#app4 .circle.active {
  animation: change 1s infinite both linear;
}

#app4 .circle .heart {
  animation: heart 2.5s cubic-bezier(0.75, 0, 0.5, 1) infinite normal;
  content: '';
  left: 50%;
  position: absolute;
  top: 50%;
}

#app4 .circle .heart-L {
  animation: heart-L 2.5s cubic-bezier(0.75, 0, 0.5, 1) infinite normal;
  background-color: #f23f5d;
  border: 1px solid #f23f5d;
  height: 1em;
  width: 1em;
  border-radius: 100%;
  content: '';
  display: block;
  position: absolute;
  transform: translate(-9px, -8px);
}

#app4 .circle .heart-R {
  animation: heart-R 2.5s cubic-bezier(0.75, 0, 0.5, 1) infinite normal;
  background-color: #f23f5d;
  border: 1px solid #f23f5d;
  height: 1em;
  width: 1em;
  border-radius: 100%;
  content: '';
  display: block;
  position: absolute;
  transform: translate(9px, -8px);
}

#app4 .circle .square {
  animation: square 2.5s cubic-bezier(0.75, 0, 0.5, 1) infinite normal;
  background-color: #f23f5d;
  border: 1px solid #f23f5d;
  height: 1em;
  width: 1em;
  content: '';
  display: block;
  position: relative;
  transform: scale(1) rotate(-45deg);
}

#app4 .circle shadow {
  animation: shadow 2.5s cubic-bezier(0.75, 0, 0.5, 1) infinite normal;
  background-color: #d7d7d7;
  border: 1px solid #d7d7d7;
  border-radius: 50%;
  bottom: -.5em;
  content: '';
  display: block;
  height: .25em;
  left: 50%;
  position: relative;
  top: 100px;
  width: 1em;
}

@keyframes square {
  50% {
    border-radius: 100%;
    transform: scale(0.5) rotate(-45deg);
  }
  100% {
    transform: scale(1) rotate(-45deg);
  }
}

@keyframes heart {
  50% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(720deg);
  }
}

@keyframes heart-L {
  60% {
    transform: scale(0.4);
  }
}

@keyframes heart-R {
  40% {
    transform: scale(0.4);
  }
}

@keyframes shadow {
  50% {
    border-color: #e4e4e4;
    transform: scale(0.5);
  }
}
